import React from "react";
import PropTypes from "prop-types";
import {BarChart, Bar, XAxis, YAxis, Tooltip, ResponsiveContainer} from "recharts";
import styles from "./ForecastChart.module.css";
import moment from "moment";

const AxisLabel = ({ style, label }) => (
    <g style={style}>
        <text>{label}</text>
    </g>
)

AxisLabel.propTypes = {
    style: PropTypes.object,
    label: PropTypes.string
}

AxisLabel.defaultProps = {
    style: {},
    label: ""
}

const XAxisStyle = { transform: "translate(50%, 100%)" };
const YAxisStyle = { transform: "rotateX(0deg) rotateY(0deg) rotateZ(-90deg) translate(-122px, 12px)" };

const ForecastChart = ({ data, forecastDate }) => (
    <div className={styles.wrapper}>
        <h2 className={styles.title}>{moment(forecastDate).format("Do MMMM")}</h2>
        <div className={styles.chartContainer}>
            <ResponsiveContainer width="100%" height={200}>
                <BarChart
                    data={data}
                    margin={{ top: 0, right: 0, left: -12, bottom: 8 }}
                >
                    <XAxis dataKey="time" label={<AxisLabel label="Time" style={XAxisStyle} />} />
                    <YAxis label={<AxisLabel label="Temperature" style={YAxisStyle} />} />
                    <Tooltip />
                    <Bar dataKey="temp" fill="#009fe3" maxBarSize={24} radius={[4, 4, 0, 0]} />
                </BarChart>
            </ResponsiveContainer>
        </div>
    </div>
);

ForecastChart.propTypes = {
    data: PropTypes.array,
    forecastDate: PropTypes.string
}

ForecastChart.defaultProps = {
    data: [],
    forecastDate: ""
}

export default ForecastChart;
